<script>

import Slick from 'vue-slick';
import 'slick-carousel/slick/slick.css';
import $ from 'jquery'

export default {
  props: ["title"],
  components: {Slick},
  methods: {
    handleBeforechange(event, slick, currentSlide, nextSlide) {

      if (nextSlide == slick.$slides.length-12){
        $('.next_arrow.catSlick').hide();
      } else {
        $('.next_arrow.catSlick').show();
      }
      if (nextSlide == 0){
        $('.prev_arrow.catSlick').hide();
      } else {
        $('.prev_arrow.catSlick').show();
      }
    },
    handleInit() {
      $('.prev_arrow.catSlick').hide();
    },
    

  },

  data(){
    return {
      catOptions: {
        slidesToShow: 11,
        slidesToScroll: 2,
        rows: 2,
        infinite: true,
        variableWidth: true,
        // centerMode: true,
        prevArrow: `<button class="prev_arrow catSlick" ref="catPrevArrow">
          <svg width="15" height="24" viewBox="0 0 15 24" fill="none" xmlns="http://www.w3.org/2000/svg">
            <path d="M2.82 0L0 2.82L9.16 12L0 21.18L2.82 24L14.82 12L2.82 0Z" fill="white"/>
          </svg>
        </button>`,
        nextArrow: `<button class="next_arrow catSlick" ref="catNextArrow">
          <svg width="15" height="24" viewBox="0 0 15 24" fill="none" xmlns="http://www.w3.org/2000/svg">
            <path d="M2.82 0L0 2.82L9.16 12L0 21.18L2.82 24L14.82 12L2.82 0Z" fill="white"/>
          </svg>
        </button>`,
        responsive: [
          {
            breakpoint: 1899,
            settings: {
              variableWidth: true,
              slidesToShow: 6,
              slidesToScroll: 2,
              infinite: true,
              rows: 2
            }
          },
        ]
      }
    }
  }
}
</script>

